.first_r1 {
    display: flex !important;
    justify-content: space-between !important;
}


@media screen and (max-width:600px) {
    .nav_logo_search {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .Left_items .nav_logo {
        width: 7rem !important;
        /* border: 1px solid red; */
    }

    .max_nav_search .header_top_righ .search_icon {
        display: none;
    }
    .max_nav_search .header_top_righ .rules {
        display: none;
    }
    .max_nav_search .header_top_righ{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .top_marque{
        /* padding: 0px 0px 0px 74px; */
        margin-left: 20%;

    }
}

@media (min-width:600px) and (max-width:7000px) {
    .nav_logo_search {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .Left_items .nav_logo {
        width: 7rem !important;
      
    }

    .nav_logo_search .header_top_righ {
        display: none;
    }
}
@media(max-width:1200px){
    .absulet{
        position: absolute;
    margin-top: -19%;
    margin-left: 30%;
    }
    .head_box{
        height: 7rem;

    }
    .offcanvas-start {
        top: 0;
        left: 0;
        width: 166px !important;
        border-right: 1px solid rgba(0,0,0,.2);
        background-color: #1627a8;
        transform: translateX(-100%);
    }
    .mob{
        padding-left: 10%;

    }
    
.navbar {
    position: absolute !important;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-top: -50px;

}
}
@media(min-width:1020px){
    .off{
        display: none;
    }
}